<script>
import topHome from '../components/Tophome'
import Carousel from '../components/Carousel'
import Tasavvur from '../components/Tasavvur'
import Hamkor from '../components/Hamkorlar'

export default {
    components: {
        topHome,
        Carousel,
        Tasavvur,
        Hamkor,
    },

    data() {
        return {
            modala: false,
            kursHome: [{
                    nomi: 'Kompyuter savodxonligi',
                    narxi: '200 ming/oy',
                    oldinginarx: '150 ming/oy',
                    soni: '1',
                    kurstur: 'Odatiy',
                    wowclass: 'wowleft',
                    img: require('../assets/ks.svg')
                },
                {
                    nomi: 'Dizaynerlik',
                    narxi: '250 ming/oy',
                    oldinginarx: '200 ming/oy',
                    soni: '1',
                    kurstur: 'Odatiy',
                    wowclass: 'wowright',
                    img: require('../assets/be.svg')

                },

                {
                    nomi: 'Front-end',
                    narxi: '250 ming/oy',
                    oldinginarx: '200 ming/oy',
                    soni: '1',
                    kurstur: 'Odatiy',
                    wowclass: 'wowleft',
                    img: require('../assets/fe.svg')


                },{
                    nomi: 'Back-end',
                    narxi: '250 ming/oy',
                    oldinginarx: '200 ming/oy',
                    soni: '1',
                    kurstur: 'Odatiy',
                    wowclass: 'wowright',
                    img: require('../assets/be.svg')


                }

            ],
            isim:false,
            fam:false,
            tel:false,
            isimph:'*Ism',
            familyaph:'*Familya',
            telph:'*Telefon raqam',
            kurs_turia: 'Odatiy',
            kurs_nomia: 'Kompyuter savodxonligi',
            kurst: false,
            kursn: false,
            kursnomlari: [
                'Kompyuter savodxonligi',
                'Web dasturlash',
                'Dizaynerlik'
            ],
            userinput:{
                name:'',
                surname:'',
                phone:'',
            }
        }
    },
    methods: {
        ModalActiver() {
            this.modala = !this.modala;
        },
        ModalActiverName(index) {
            this.modala = !this.modala;
            this.kurs_nomia = this.kursHome[index].nomi;
            this.kurs_turia = this.kursHome[index].kurstur;

        },
        kurstopen() {
            this.kurst = !this.kurst;
        },
        kursnopen() {
            this.kursn = !this.kursn;
        },
        kursnChange(index) {
            this.kurs_nomia = this.kursnomlari[index]
        },
        change(event) {
            let a = []
            if (event.target.value.length < 10) {
                event.target.value = event.target.value.replace(/[^0-9]/g, "")
                this.errort = false
            }
            if (event.target.value.length == 9) {
                a = event.target.value.split("")
                this.oldraqam = a[0] + a[1] + "-" + a[2] + a[3] + a[4] + "-" + a[5] + a[6] + "-" + a[7] + a[8]
                event.target.value = this.oldraqam
            }
            if (event.target.value.length > 12) {
                event.target.value = this.oldraqam
            }
        },
        checkForm(){
            if (this.tanlangankurs != '*Kompyuter savodxonligi' && this.userinput.name != '' && this.userinput.surname  != '' && this.userinput.phone  != '') {
                return true
            }
            else{ 
                return false
            }  
        }
    },
};
</script>

