import Vue from 'vue'
import VueRouter from 'vue-router'


import Home from '../views/Home'
import About from '../views/About'
import Kontakt from '../views/Kontakt'
import Kurslar from '../views/Kurslar'

Vue.use(VueRouter)

const routes = [{
        path: '/',
        name: 'Home',
        component: Home
    },
    {
        path: '/About',
        name: 'BizHaqimizda',
        component: About
    },
    {
        path: '/Kontakt',
        name: 'Kontakt',
        component: Kontakt
    },
    {
        path: '/Kurslar',
        name: 'Kurslar',
        component: Kurslar
    },
]





const router = new VueRouter({
    routes
})

export default router